import { Combobox } from '@headlessui/react';
import clsx from 'clsx';
import React from 'react';
import { twMerge } from 'tailwind-merge';
import { getDistanceToBottom } from '../../utils/getDistanceToBottom';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownFilterProps } from './DropDownFilter.interface';

const MENU_HEIGHT = 268;

const highlightQuery = (string: string, query: string) => {
  const regex = new RegExp(query, 'gi');
  return string.replace(regex, (match) => '<b>' + match + '</b>');
};

const getOptionItemClasses = (hovered: boolean, isActive: boolean) => {
  return clsx(
    'zep-flex zep-w-full zep-items-center zep-justify-between zep-bg-background-light zep-px-1 zep-py-0.75',
    { 'zep-bg-background-medium': hovered },
    { 'zep-bg-background-medium': isActive },
    { 'zep-pr-3': isActive },
  );
};

export const DropDownFilter: React.FC<DropDownFilterProps> = ({
  disabled = false,
  items,
  label,
  message,
  noOptionsLabel,
  placeholder,
  readOnly = false,
  state = 'default',
  ...props
}) => {
  const menuRef = React.useRef(null);
  const inputRef = React.useRef(null);
  const hasError = state === 'error';
  const hasInfo = state === 'info';
  const [query, setQuery] = React.useState('');
  const [selectedItem, setSelectedItem] = React.useState(null);
  const hasInput = query !== '' || selectedItem !== null;
  const disabledOrReadonly = disabled || readOnly;
  const iconColor = hasError ? 'zep-text-error' : hasInfo ? 'zep-text-info' : '';

  const filteredItems =
    query === ''
      ? items
      : items.filter((item) => {
          return item.name.toLowerCase().includes(query.toLowerCase());
        });

  return (
    <div
      className={twMerge(
        clsx('zep-relative', 'zep-inline-block', 'zep-text-left', 'zep-w-full', {
          'zep-cursor-not-allowed zep-opacity-disabled': disabled,
        }),
        props.className,
      )}
      data-testid="zep-drop-down-filter"
      {...props}
    >
      <Combobox as="div" value={selectedItem} onChange={setSelectedItem} disabled={disabledOrReadonly}>
        {({ open }) => {
          const distanceToBottom = getDistanceToBottom(inputRef.current);
          const openOnTop = distanceToBottom < MENU_HEIGHT;
          const color = disabled ? 'zep-text-opacity-disabled' : 'zep-text-typography-dark-100';

          const inputClasses = clsx(
            'zep-bg-background-light',
            'zep-inline-flex',
            'zep-justify-between',
            'zep-rounded-4',
            'zep-border-solid',
            'zep-pl-1',
            'zep-py-0.75',
            'zep-font-roboto',
            'zep-w-full',
            'zep-outline-none',
            'zep-truncate',
            hasError ? 'zep-border-2 zep-border-error' : 'zep-border-1 zep-border-neutral-dark-default',
            hasInput ? 'zep-pr-4' : 'zep-pr-0.75',
            color,
            'placeholder:zep-text-typography-dark-100/70',
            { 'zep-ring': open },
            { 'zep-bg-background-medium': readOnly },
            { 'zep-border-opacity-disabled': disabled },
            { 'zep-active:zep-ring': !disabledOrReadonly },
            { 'zep-ring-offset-1': !disabledOrReadonly },
            { 'focus-visible:zep-ring': !disabledOrReadonly },
            { 'zep-ring-focus': !disabledOrReadonly },
            { 'zep-cursor-not-allowed': disabledOrReadonly },
          );

          const optionContainerClasses = clsx(
            'zep-absolute',
            'zep-shadow-sm',
            'zep-py-0.25',
            'zep-z-10',
            'zep-right-0',
            'zep-w-full',
            'zep-max-h-[268px]',
            'zep-origin-top-right',
            'zep-rounded-4',
            'zep-bg-background-light',
            'zep-ring-1,',
            'zep-ring-tertiary',
            'zep-ring-opacity-1',
            'focus:outline-none',
            'zep-overflow-hidden',
            'zep-overflow-y-auto',
            openOnTop ? 'zep-mb-[-25px]' : 'zep-mt-0.25',
            { 'zep-bottom-full': openOnTop },
          );

          const deleteBtnClasses = clsx(
            'zep-absolute',
            'zep-top-0.75',
            'zep-right-2.5',
            'zep-flex',
            'zep-items-center',
          );

          const iconClasses = clsx(
            disabledOrReadonly ? 'zep-text-opacity-disabled' : 'zep-text-typography-dark-100',
            'zep-shrink-0',
            'zep-transition-transform',
            'zep-duration-300',
            {
              'zep-rotate-180': open,
            },
          );

          return (
            <>
              {label && (
                <Combobox.Label className={clsx('zep-block', 'zep-mb-0.5', { 'zep-text-opacity-disabled': disabled })}>
                  {label}
                </Combobox.Label>
              )}
              <div className="zeb-block zep-relative zep-mt-0.5">
                <Combobox.Input
                  className={inputClasses}
                  autoComplete="off"
                  placeholder={readOnly ? undefined : placeholder}
                  readOnly={readOnly}
                  ref={inputRef}
                  data-testid="zep-drop-down-filter-input"
                  onChange={(event) => setQuery(event.target.value)}
                />
                {Boolean(selectedItem) && (
                  <button
                    className={deleteBtnClasses}
                    data-testid="zep-drop-down-filter-delete"
                    disabled={disabledOrReadonly}
                    onClick={() => {
                      setQuery('');
                      setSelectedItem(null);
                    }}
                  >
                    <FunctionalIcon className={color} name="delete-circle" size={24} />
                  </button>
                )}
                <Combobox.Button className="zep-absolute zep-inset-y-[0px] zep-right-[0px] zep-flex zep-items-center zep-pr-0.75">
                  <FunctionalIcon className={iconClasses} name="chevron-mini-down" role="img" size={24} />
                </Combobox.Button>
                {filteredItems.length > 0 ? (
                  <Combobox.Options
                    className={optionContainerClasses}
                    ref={menuRef}
                    data-testid="zep-drop-down-filter-menu"
                  >
                    {filteredItems.map((item) => (
                      <Combobox.Option
                        key={item.id}
                        data-testid="zep-drop-down-filter-option"
                        value={item.name}
                        className={({ active, selected }) => getOptionItemClasses(active, selected)}
                      >
                        {({ selected }) => (
                          <>
                            <span
                              className="zep-truncate"
                              dangerouslySetInnerHTML={{ __html: highlightQuery(item.name, query) }}
                            />
                            {selected && (
                              <FunctionalIcon name="check" role="img" size={24} className="zep-absolute zep-right-1" />
                            )}
                          </>
                        )}
                      </Combobox.Option>
                    ))}
                  </Combobox.Options>
                ) : (
                  <Combobox.Options className={optionContainerClasses} ref={menuRef}>
                    <Combobox.Option
                      data-testid="zep-drop-down-filter-no-options"
                      value={0}
                      className={clsx('zep-block zep-py-0.75 zep-text-center')}
                    >
                      {noOptionsLabel}
                    </Combobox.Option>
                  </Combobox.Options>
                )}
              </div>
            </>
          );
        }}
      </Combobox>
      {message && (
        <div
          className={clsx(
            'zep-mt-0.25',
            { 'zep-text-typography-dark-100': !hasError && !hasInfo },
            'zep-text-0.875',
            'zep-flex',
            iconColor,
          )}
        >
          {state !== 'default' && (
            <FunctionalIcon
              name={state === 'error' ? 'warning-triangle-filled' : 'info-circle-filled'}
              role="img"
              size={24}
              className="zep-mr-0.5 zep-shrink-0"
            />
          )}
          <span className="zep-mt-0.125">{message}</span>
        </div>
      )}
    </div>
  );
};
